* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  list-style: none;
  margin-right: auto;
  padding: 0;
}
.country-list_item,
.country_item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-list_img,
.country_img {
  margin-right: 10px;
}
.country-list_name,
.country_item {
  font-size: 20px;
  font-weight: 500;
}
.country_info {
  font-weight: 200;
}
.container {
  display: flex;
  align-items: center;
}
